"use client";

import React, { useEffect, useRef, useState } from "react";

interface VoiceInterfaceProps {
  className?: string;
}

export default function VoiceInterface({ className = "" }: VoiceInterfaceProps) {
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const chunksRef = useRef<BlobPart[]>([]);
  const [recording, setRecording] = useState(false);
  const [status, setStatus] = useState<string>("Ready to listen");
  const [transcript, setTranscript] = useState<string>("");
  const [reply, setReply] = useState<string>("");
  const [isListening, setIsListening] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    return () => {
      if (mediaRecorderRef.current && mediaRecorderRef.current.state !== "inactive") {
        mediaRecorderRef.current.stop();
      }
    };
  }, []);

  async function startRecording() {
    setTranscript("");
    setReply("");
    setStatus("Requesting microphone access...");
    setIsListening(true);

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(stream, { mimeType: "audio/webm" });
      chunksRef.current = [];

      recorder.ondataavailable = (e) => {
        if (e.data.size > 0) chunksRef.current.push(e.data);
      };

      recorder.onstop = async () => {
        const blob = new Blob(chunksRef.current, { type: "audio/webm" });
        const form = new FormData();
        form.append("audio", blob, "audio.webm");
        setStatus("Processing your request...");

        try {
          const res = await fetch("/api/voice/turn", { method: "POST", body: form });
          if (!res.ok) {
            setStatus("Error processing request");
            return;
          }

          const audioBuf = await res.arrayBuffer();
          const transcriptHdr = res.headers.get("x-transcript");
          const replyHdr = res.headers.get("x-text");
          setTranscript(transcriptHdr ? decodeURIComponent(transcriptHdr) : "");
          setReply(replyHdr ? decodeURIComponent(replyHdr) : "");

          const url = URL.createObjectURL(new Blob([audioBuf], { type: res.headers.get("content-type") || "audio/mpeg" }));
          if (audioRef.current) {
            audioRef.current.src = url;
            await audioRef.current.play().catch(() => {});
          }
          setStatus("Response ready");
        } catch (error) {
          console.error("Error:", error);
          setStatus("Error processing request");
        }
      };

      recorder.start();
      mediaRecorderRef.current = recorder;
      setRecording(true);
      setStatus("Listening...");
    } catch (error) {
      console.error("Error accessing microphone:", error);
      setStatus("Microphone access denied");
      setIsListening(false);
    }
  }

  function stopRecording() {
    if (mediaRecorderRef.current && mediaRecorderRef.current.state !== "inactive") {
      mediaRecorderRef.current.stop();
    }
    setRecording(false);
    setIsListening(false);
    setStatus("Processing...");
  }

  return (
    <div className={`bg-white rounded-lg shadow-lg p-8 ${className}`}>
      <div className="text-center mb-8">
        <h2 className="text-2xl font-bold text-gray-800 mb-2">
          Voice Assistant
        </h2>
        <p className="text-gray-600">
          Speak naturally about your financial questions or concerns
        </p>
      </div>

      {/* Voice Input Section */}
      <div className="mb-8">
        <div className="flex justify-center mb-6">
          <button
            className={`
              w-32 h-32 rounded-full border-4 transition-all duration-300 flex items-center justify-center
              ${isListening
                ? 'border-red-500 bg-red-50 animate-pulse'
                : 'border-blue-500 bg-blue-50 hover:bg-blue-100'
              }
            `}
            onMouseDown={startRecording}
            onMouseUp={stopRecording}
            onTouchStart={(e) => { e.preventDefault(); startRecording(); }}
            onTouchEnd={(e) => { e.preventDefault(); stopRecording(); }}
          >
            <div className="text-center">
              <i className={`fas fa-microphone text-3xl mb-2 ${
                isListening ? 'text-red-500' : 'text-blue-500'
              }`}></i>
              <div className="text-sm font-medium text-gray-700">
                {isListening ? 'Listening...' : 'Hold to Speak'}
              </div>
            </div>
          </button>
        </div>

        <div className="text-center">
          <div className={`text-sm font-medium ${
            isListening ? 'text-red-600' : 'text-gray-500'
          }`}>
            {status}
          </div>
        </div>
      </div>

      {/* Transcript and Response */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="space-y-2">
          <h3 className="text-lg font-semibold text-gray-800 flex items-center">
            <i className="fas fa-user mr-2 text-blue-500"></i>
            You said
          </h3>
          <div className="min-h-[120px] p-4 border border-gray-200 rounded-lg bg-gray-50">
            {transcript || (
              <div className="text-gray-400 italic">
                Your spoken words will appear here...
              </div>
            )}
          </div>
        </div>

        <div className="space-y-2">
          <h3 className="text-lg font-semibold text-gray-800 flex items-center">
            <i className="fas fa-robot mr-2 text-green-500"></i>
            Assistant response
          </h3>
          <div className="min-h-[120px] p-4 border border-gray-200 rounded-lg bg-green-50">
            {reply || (
              <div className="text-gray-400 italic">
                AI responses will appear here...
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Audio Player */}
      {reply && (
        <div className="mt-6 pt-6 border-t border-gray-200">
          <audio ref={audioRef} controls className="w-full" />
        </div>
      )}
    </div>
  );
}
